<template>
    <div class="Mycard">
        <div v-if="card.length!=0">
            <div class="swiper-container Mycardbox">
                <div class="swiper-wrapper">
                    <div class="swiper-slide" v-for="(item,index) in card" :key="index">
                        <strong class="num">
                            {{item.roomNum}}
                        </strong>
                        <span class="name">{{item.hotelName}}</span>
                    </div>
                </div>
            </div>
            <section class="information">
                <h4>{{card[activecard].customerName}}，您当前有{{card.length}}张房卡
                    <span class="fr">{{activecard+1}}/{{card.length}}</span>
                </h4>
                <p>{{card[activecard].roomNum}}房当前享有：
                    <template v-if="card[activecard].breakfastQuantity!=0">
                        {{card[activecard].breakfastQuantity}}张早餐券；
                    </template>
                    <template v-else>
                        无早餐券；
                    </template>
                    <template v-if="card[activecard].freeDepositEnable==0">
                        免押金；
                    </template>

                    最晚退房时间为{{card[activecard].vipCheckOut}}点之前。</p>
            </section>
            <div class="weui-grids">
                <!-- <a href="javascript:;" class="weui-grid" @click="Unlock=true">
                    <div class="weui-grid__icon">
                        <img src="../assets/svg/Key.svg" alt="">
                    </div>
                    <p class="weui-grid__label">一键开锁</p>
                </a> -->
                <a href="javascript:;" class="weui-grid" @click="codefn" v-if="card[activecard].breakfastCoupons.length!=0">
                    <div class="weui-grid__icon">
                        <img src="../assets/svg/breakfast.svg" alt="">
                    </div>
                    <p class="weui-grid__label">早餐券</p>
                </a>
                <a href="javascript:;" class="weui-grid" @click="dialog1=true">
                    <div class="weui-grid__icon">
                        <img src="../assets/svg/See.svg" alt="">
                    </div>
                    <p class="weui-grid__label">费用明细</p>
                </a>
            </div>
        </div>
        <div class="js_dialog" id="iosDialog" v-if="Unlock">
            <div class="weui-mask"></div>
            <div class="weui-dialog">
                <div class="weui-dialog__hd">
                    <strong class="weui-dialog__title">温馨提示</strong>
                </div>
                <div class="weui-dialog__bd">请确保在房门口进行开锁，是否确认开锁？</div>
                <div class="weui-dialog__ft">
                    <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_default" @click="Unlock=flase">否</a>
                    <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_primary">是</a>
                </div>
            </div>
        </div>
        <Success v-if="isok" text="已开锁"></Success>
        <div class="code-box" v-if="code">
            <span class="Close" @click="code=false">关闭</span>
            <div class="code">
                <p class="title">向店员出示此二维码</p>
                <div class="swiper-container" id="code">
                    <div class="swiper-wrapper">
                        <div class="swiper-slide" v-for="(item,index) in breakfastCoupons" :key="index">
                            <div class="breakfast"></div>
                            <!-- <img src="../assets/images/room_7.png" alt=""> -->
                            <p class="num">{{item.code |strchange}}</p>
                        </div>
                    </div>
                    <p class="Prompt">{{card[activecard].roomNum}}房当前有{{breakfastCoupons.length}}张早餐券可用，左右滑动下一张</p>
                    <p class="activeNum">{{activecard1}}/{{breakfastCoupons.length}}</p>
                </div>
            </div>
            <div class="js_dialog" v-if="dialog1">
                <div class="weui-mask" @click="dialog1=false"></div>
                <div class="weui-dialog Paymentdetails">
                    <i class="weui-icon-cancel" @click="dialog1=false"></i>
                    <div class="weui-dialog__hd">
                        费用明细
                    </div>
                    <div class="weui-dialog__bd">
                        <h4 class="title mt15">费用明细</h4>
                        <div class="weui-flex">
                            <div class="weui-flex__item">
                                <div class="placeholder">
                                    房费*1
                                </div>
                            </div>
                            <div class="weui-flex__item right">
                                <div class="placeholder">
                                    +￥{{card[activecard].roomPrice |money}}
                                </div>
                            </div>
                        </div>
                        <div class="weui-flex">
                            <div class="weui-flex__item">
                                <div class="placeholder">
                                    押金*1
                                </div>
                            </div>
                            <div class="weui-flex__item right">
                                <div class="placeholder">
                                    +￥{{card[activecard].deposit |money}}
                                </div>
                            </div>
                        </div>
                        <div class="weui-flex" v-if="card[activecard].fbDiscount&&card[activecard].fbDiscount!=0">
                            <div class="weui-flex__item">
                                <div class="placeholder">
                                    粉币
                                </div>
                            </div>
                            <div class="weui-flex__item right">
                                <div class="placeholder">
                                    -￥{{card[activecard].fbDiscount |money}}
                                </div>
                            </div>
                        </div>
                        <div class="weui-flex" v-if="card[activecard].couponsDiscount&&card[activecard].couponsDiscount!=0">
                            <div class="weui-flex__item">
                                <div class="placeholder">
                                    优惠券
                                </div>
                            </div>
                            <div class="weui-flex__item right">
                                <div class="placeholder">
                                    -￥{{card[activecard].couponsDiscount |money}}
                                </div>
                            </div>
                        </div>
                        <div class="weui-flex" v-if="card[activecard].integralDiscount&&card[activecard].integralDiscount!=0">
                            <div class="weui-flex__item">
                                <div class="placeholder">
                                    积分
                                </div>
                            </div>
                            <div class="weui-flex__item right">
                                <div class="placeholder">
                                    -￥{{card[activecard].integralDiscount |money}}
                                </div>
                            </div>
                        </div>
                        <div class="weui-flex">
                            <div class="weui-flex__item">
                                <div class="placeholder">
                                    合计
                                </div>
                            </div>
                            <div class="weui-flex__item right">
                                <div class="placeholder">
                                    +￥{{card[activecard].totalMoney |money}}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <Nodata v-if="Nodata" msg='暂无数据'></Nodata>
        </div>
    </div>
</template>

<script type='text/ecmascript-6'>
import qr from "@/assets/js/qrcode.min.js";
import { common, Personal } from "@/assets/js/request.js";
import Success from "@/components/Success";
import Nodata from "@/components/Nodata.vue";
export default {
  data() {
    return {
      hotelId: sessionStorage.getItem("hotelId"),
      Unlock: false,
      activecard: 0,
      isok: false,
      code: false,
      dialog1: false,
      quit: false,
      card: [],
      Nodata: false,
      breakfastCoupons: [],
      activecard1: 1
    };
  },
  mounted() {
    let _this = this;
    Personal.roomCard({
      Suffix: this.hotelId + "/roomCard",
      params: {
        pageSize: 99
      },
      fn: data => {
        if (data.records.length != 0) {
          this.card = data.records;
          this.card.forEach(item => {
            item.totalMoney = Number(item.roomPrice) + Number(item.deposit);
            if (item.fbDiscount) {
              item.totalMoney =
                Number(item.totalMoney) - Number(item.fbDiscount);
            }
            if (item.couponsDiscount) {
              item.totalMoney =
                Number(item.totalMoney) - Number(item.couponsDiscount);
            }
            if (item.integralDiscount) {
              item.totalMoney =
                Number(item.totalMoney) - Number(item.integralDiscount);
            }
          });
          setTimeout(() => {
            new Swiper(".Mycardbox", {
              slidesPerView: "auto",
              centeredSlides: true, //居中
              paginationClickable: true, //点击分页器的指示点分页器会控制Swiper切换。
              spaceBetween: 10,
              loop: true,
              autoplayDisableOnInteraction: false,
              onSlideChangeStart: function(swiper) {
                _this.activecard = swiper.activeIndex % _this.card.length;
              }
            });
          }, 100);
        } else {
          this.Nodata = true;
        }
      }
    });
  },
  methods: {
    codefn() {
      this.breakfastCoupons = this.card[this.activecard].breakfastCoupons;
      var _this = this;
      _this.code = true;
      common.getconfig({
        fn: data => {
          data.hostUrl;
          this.breakfastCoupons.forEach((item, index) => {
            new qr(
              document.getElementsByClassName("breakfast")[index],
              data.hostUrl +
                "mobile/78CDF1/my/" +
                _this.hotelId +
                "/writeOffBQ/" +
                item.id
            );
          });
        }
      });

      setTimeout(() => {
        var mySwiper = new Swiper("#code", {
          onSlideChangeStart: function(swiper) {
            //   _this.activecard = swiper.activeIndex % _this.card.length;
            // console.log(swiper.activeIndex);
            _this.activecard1 = swiper.activeIndex + 1;
          }
        });
      }, 100);
    }
  },
  components: {
    Success,
    Nodata
  }
};
</script>

<style scoped lang='less' rel='stylesheet/less'>

</style>



